<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Registrar nuevo categoria:</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Letra"
						  label-for="letra"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="letra"
							  v-model="categoria.letra"
							  type="text"
							  placeholder="Ingrese la letra de la categoria"
							  :state="validacion.letra.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="patente-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Limite"
						  label-for="limite"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="limite"
							  v-model="categoria.limite"
							  type="decimal"
							  placeholder="Ingrese el limite de la categoria"
							  :state="validacion.limite.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="limite-live-feedback">
					{{ validacion.limite.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button
			class="mt-2"
			variant="success"
			block
				  :disabled="botonalta"
			@click="postCategoria()"
			>Guardar</b-button
		>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";

	export default {
		props: {
			updateTable: Function,
		},
		data() {
			return {
				categoria: {},
				data: {},
				botonalta: false,
				validacion: {
					letra: { estado: null, mensaje: "" },
                    limite: { estado: null, mensaje: "" },
				},
				respuesta: {},
			};
		},
		methods: {
			async getCategorias() {
                let CategoriasAPI = new APIControler();
                this.data = await CategoriasAPI.getData();
			},
			async postCategoria() {
                this.botonalta = true;
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "categorias/";
				this.respuesta = await gastoSalienteAPI.postData(this.categoria);
                if (respuesta !== undefined) {
                    this.botonalta = false
                }
				this.cargarFeedback();
				this.updateTable();
			},

			cargarFeedback() {
				let valido;
				this.respuesta=this.respuesta|| {}
				for (let key in this.validacion) {
					valido = !this.respuesta.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					//console.log(key);
					if (!valido) this.validacion[key].mensaje = this.respuesta[key][0];
				}
			},
		},
	};
</script>

<style></style>
