<template>
	<div>
		<h6>Los campos en (*) son obligatorios</h6>
		<h4>Datos</h4>

		<b-form>
			<!-- nro_ticket -->
			<b-form-group label="*Letra"
						  label-for="letra"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="letra"
							  v-model="categoria.letra"
							  type="text"
							  placeholder="Ingrese la letra de la categoria"
							  :state="validacion.letra.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="patente-live-feedback">
					{{ validacion.letra.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>

			<b-form-group label="*Limite"
						  label-for="limite"
						  @submit.stop.prevent="handleSubmit">
				<b-form-input id="limite"
							  v-model="categoria.limite"
							  type="decimal"
							  placeholder="Ingrese el limite de la categoria"
							  :state="validacion.limite.estado"
							  invalid-feedback="Complete este campo"
							  required>
				</b-form-input>
				<b-form-invalid-feedback id="limite-live-feedback">
					{{ validacion.limite.mensaje }}
				</b-form-invalid-feedback>
			</b-form-group>
		</b-form>
		<b-button class="mt-2"
				  variant="success"
				  block
				  @click="putCategoria()">Modificar</b-button>
	</div>
</template>

<script>
	import { APIControler } from "@/store/APIControler";
	import axios from "axios";

	export default {
		props: {
			categoria: {},
			updateTable: Function,
		},
		data() {
			return {
                categorias: {},
				data: {},
				options: [{ value: null, text: "Elija una categoria", disabled: true }],

				validacion: {
					letra: { estado: null, mensaje: "" },
                    limite: { estado: null, mensaje: "" },
				},
			};
		},
		created: function () {
			this.getCategorias();
		},
		methods: {
            async getCategorias() {
				let gastoSalienteAPI = new APIControler();
				gastoSalienteAPI.apiUrl.pathname = "categorias/";
				this.data = await gastoSalienteAPI.getData(this.categorias);
				this.data.forEach((element) => {
					let option = {};
					option.value =
						"http://93.188.162.174:8081/categorias/" + element.id_categoria + "/";
					option.text = element.categorias;
					console.log(option);
					this.options.push(option);
				});
			},

			async putCategoria() {
				let respuesta = "vacio";
				await axios
					.put(
						"http://93.188.162.174:8081/categorias/" +
							this.categoria.id_categoria +
							"/",
						this.categoria
					)
					.then(function (data) {
						swal("Operación Exitosa", " ", "success");
					})
					.catch(function (error) {
						const mje =
							error.response.status < 500
								? "Los datos no son válidos"
								: "Se ha detectado un problema ";
						swal("¡ERROR!", mje, "error");
						respuesta = error.response.data;
					});
				this.cargarFeedback(respuesta);
				this.updateTable();
				// console.log("respuesta:");
				// console.log(respuesta);
			},

			cargarFeedback(respuestaAPI) {
				console.log("respuestaAPI");
				console.log(respuestaAPI);
				let valido;
				for (let key in this.validacion) {
					valido = !respuestaAPI.hasOwnProperty(key);
					this.validacion[key].estado = valido;
					console.log(key);

					if (!valido) this.validacion[key].mensaje = respuestaAPI[key][0];
				}
			},
		},
	};
</script>

<style></style>
